import { request, requestDownload } from 'utils/index';

const prefix = '/api/students';

export const getStudentBonusEstimation = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  includeRetest,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/BonusNote/BrowseStudentForBonusEstimation',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      includeRetest,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });



export const addBonusEstimation = ({
  semesterId,
  yearEduId,
  students
} = {}) =>
  request({
    prefix,
    url: '/BonusNote/CreateBonusEstimation',
    method: 'POST',
    params: {},
    data: {
      semesterId,
      yearEduId,
      students
    }
  });


export const getNotedBonus = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId,
  page,
  pageSize=10,
  orderBy,
  isPagingEnabled=true
} = {}) =>
  request({
    prefix,
    url: '/BonusNote/BrowseBonusEstimationStudent',
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId,
      page,
      pageSize,
      orderBy,
      isPagingEnabled
    },
    data: {}
  });


export const deleteBonusEstimation = ({
  semesterId,
  yearEduId,
  students
} = {}) =>
  request({
    prefix,
    url: '/BonusNote/DeleteBonusEstimation',
    method: 'DELETE',
    params: {},
    data: {
      semesterId,
      yearEduId,
      students
    }
  });


export const exportBonusEstimation = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  semesterId,
  yearEduId
} = {}) =>
  requestDownload({
    prefix,
    url: '/BonusNote/ExportBonusEstimation',
    method: 'POST',
    params: {},
    data: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      semesterId,
      yearEduId
    },
    defaultFileName: 'ExportBonusEstimation.xlsx'
  });


